.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  color: #fbff00;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-button {
  width: 40vmin;
  height: 15vmin;
  margin: 10px;
  font-size: 10vmin;
  border-radius: 5vmin;
}

.App-header {
  background-color: #e48111;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(2px + 4vmin);
}

.App-link {
  color: #61dafb;
}

.App-text {
  color: #61dafb;
  font-size: 12vw;
  margin: 0;
  padding: 0;
}

.App-form {
  font-size: 10vmin;
  width: 20vmin;
  grid-row: 1;
  border-radius: 5vmin;
}

.App-inner {
  font-size: 6vmin;
  width: 80vmin;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
